import './header.css';
import Button from './button'

const Header = ({ title, onAdd, showAdd }) => {
	/*const onclick = () => {
		console.log("Click");
	}*/
	return (
		<header className = 'header'>
			<h1>{title}</h1>
			<Button color = {showAdd ? 'red' : 'green'} 
			text = {showAdd ? 'Close' : 'Add'} 
			onclick = {onAdd} />
		</header>
		)
}

Header.defaultProps = {
	title: 'Task Tracker',
}

export default Header